const app = getApp()

export default {
	data() {
		return {
			scrollTop: 0,
			followlist: [],
			followindex: 0,
			current: 0,
			more: true,
			tabs: [
				"关注"
			]
		};
	},
	onLoad() {
		this.loadpage()
		uni.showShareMenu({})
	},
	onPageScroll(res) {
		this.scrollTop = res.scrollTop
	},
	onPullDownRefresh() {
		this.followindex = 0
		this.more = true
		this.loadpage()
	},
	onReachBottom() {
		this.loadpage()
	},
	onShareAppMessage(res) {
		return {
			title: app.globalData.sharetitle,
			path: '/pages/index/index',
			imageUrl: app.globalData.shareimg
		}
	},
	methods: {
		loadpage() {
			if (this.more) {
				uni.request({
					url: app.globalData.apiurl + 'users/follow', //仅为示例，并非真实接口地址。
					data: {
						index: this.followindex
					},
					header: {
						'content-type': 'application/json', //自定义请求头信息
						'cookie': app.globalData.cookie
					},
					success: (res) => {
						console.log(res);
						if (this.followindex == 0) {
							this.followlist = []
						}
						res.data.list.forEach((item, index) => {
							item.follow = true
							item.delay = '' + (index + 5) * 0.1 + 's'
							setTimeout(() => {
								item.delay = undefined
							}, (index + 11) * 100)
						})
						this.followlist = this.followlist.concat(res.data.list)
						if (this.followindex != 0) {
							setTimeout(() => {
								uni.pageScrollTo({
									scrollTop: this.scrollTop + 300,
									duration: 500,
								})
								console.log("top" + this.scrollTop)
							}, 200)
						}
						this.more = res.data.more
						this.followindex += res.data.list.length
						uni.stopPullDownRefresh()
					}
				})
			}
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		tabSelect(e) {
			this.current = e.currentTarget.dataset.id;
		},
		navChange(index) {
			this.current = index;
		},
		swiperChange(e) {
			this.current = e.detail.current;
		},
		sponsorPage(index) {
			uni.navigateTo({
				url: "../sponsor/sponsor?id=" + this.followlist[index].name
			})
		},
		follow(index) {
			uni.request({
				url: app.globalData.apiurl + 'users/follow/' + this.followlist[index].name, //仅为示例，并非真实接口地址。
				method: 'POST',
				header: {
					'content-type': 'application/json', //自定义请求头信息
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					console.log(res.data);
					this.followlist[index].follow = res.data.follow
				}
			})
		}
	}
}
